<template>
  <div class="address">
    <ele-head :isLeft="true" title="选择收货地址"></ele-head>
    <div class="city-search">
      <div class="search">
        <span class="city" @click="$router.push('/city')">
          {{city}}
          <i class="fa fa-angle-down"></i>
        </span>|
        <i class="fa fa-search"></i>
        <input v-model="searchVal" type="text" placeholder="小区/写字楼/学校等">
      </div>
      <location :address="address" @click="selectAddress"></location>
    </div>
    <div class="area">
      <ul class="area-list" v-for="(item, index) in areaList" :key="index">
        <li @click="selectAddress(item)">
          <h4>{{item.name}}</h4>
          <p>{{item.district}}-{{item.address}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import EleHead from '../components/EleHead.vue'
import Location from '../components/Location.vue'
export default {
  name: 'address',
  components: {
    EleHead,
    Location
  },
  data () {
    return {
      city: '', // 当前城市
      searchVal: '',
      areaList: []
    }
  },
  computed: {
    address () {
      return this.$store.getters.location.formattedAddress
    }
  },
  watch: {
    searchVal () {
      this.searchPlace()
    }
  },
  methods: {
    searchPlace () {
      const self = this
      AMap.plugin('AMap.Autocomplete', function () {
        // 实例化Autocomplete
        var autoOptions = {
          // city 限定城市，默认全国
          city: self.city
        }
        var autoComplete = new AMap.Autocomplete(autoOptions)
        autoComplete.search(self.searchVal, function (status, result) {
          // 搜索成功时，result即是对应的匹配数据
          self.areaList = result.tips
        })
      })
    },
    selectAddress (item) {
      if (item) {
        // 设置地址
        this.$store.dispatch(
          'setAddress',
          item.district + item.address + item.name
        )
      } else {
        this.$store.dispatch('setAddress', this.address)
      }
      // 跳转home
      this.$router.push('/home')
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.city = to.params.city
    })
  }
}
</script>

<style scoped>
.address {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}
.city-search {
  background-color: #fff;
  padding: 10px 20px;
  color: #333;
}
.search {
  background-color: #eee;
  height: 40px;
  border-radius: 10px;
  box-sizing: border-box;
  line-height: 40px;
}
.search .city {
  padding: 0 10px;
}
.city i {
  margin-right: 10px;
}
.search input {
  margin-left: 5px;
  background-color: #eee;
  outline: none;
  border: none;
}
.area {
  margin-top: 16px;
  background-color: #fff;
}
.area li {
  border-bottom: 1px solid #eee;
  padding: 8px 16px;
  color: #aaa;
}
.area li h4 {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}
</style>
